<template>
    
    <div class="d-flex bd-highlight">
                                                    
        <div class="me-auto">
            <span class="text-muted">
                Página {{ conf.page }} de {{ conf.totalPages }}
            </span>
        </div>

        <div class="ms-auto">
            <div class="btn-group" role="group" aria-label="">
                <button v-if="buttonPreviousPage(conf)" type="button" class="btn btn-outline-secondary btn-sm btn-pager me-2" @click="previousPage(conf)" >
                    <i class="fa-solid fa-chevron-left"></i>
                </button>

                <button v-for="nPage in range(conf.initialPage, conf.endPage)" :key="nPage" type="button" :class="classPager(conf, nPage)" @click="page(nPage, conf)">
                    {{ nPage }}
                </button>

                <button v-if="buttonNextPage(conf)" type="button" class="btn btn-outline-secondary btn-sm btn-pager ms-2" @click="nextPage(conf)">
                    <i class="fa-solid fa-chevron-right"></i>
                </button>
            </div>
        </div>

    </div>

</template>

<script>
    
import { onMounted, ref } from 'vue';

export default {
    name: 'PagerApp',
    props: [ 'pagerconf', 'totaldata' ],
    setup (props) {

        const conf = ref(props.pagerconf);

        onMounted(() => {
            // createPager(conf.value);
        });

        const classPager = (pager, page) => {
            if (pager.page === page) {
                return 'btn btn-outline-secondary btn-sm btn-pager page-active';
            }

            if (pager.page < pager.totalPages) {
                return 'btn btn-outline-secondary btn-sm btn-pager';
            }

            if (pager.page === pager.totalPages) {
                return 'btn btn-outline-secondary btn-sm btn-pager';
            }
        };

        const range = (start, end) => {
            return Array(end - start + 1).fill().map((_, idx) => start + idx)
        }

        const buttonPreviousPage = (pager) => {
            if (pager.page === 1) {
                return false
            }

            return true;
        };

        const previousPage = (pager) => {

            const nPage = pager.page - 1;
            pager.page = nPage;

            const block = pager.block;

            if ((block > 1) && (nPage < (((block - 1) * pager.blocksPerPage) + 1))) {
                previousBlock(pager);
            } else {
                page(nPage, pager);
            }

        }

        const previousBlock = (pager) => {

            const block = pager.block - 1;
            pager.block = block;

            const nPage = ((block - 1) * pager.blocksPerPage) + 1;
            pager.page = nPage;

            pager.initialPage = nPage;

            const endPage = (pager.initialPage + pager.blocksPerPage) - 1;

            if (endPage <= pager.totalPages) {
                pager.endPage = endPage;
            } else {
                pager.endPage = pager.totalPages;
            }

            page(nPage, pager);

        }

        const buttonNextPage = (pager) => {
            if ( (pager.page === pager.totalPages) ) {
                return false
            }

            return true;
        };

        const nextPage = (pager) => {

            const nPage = pager.page + 1;
            pager.page = nPage;

            const block = pager.block;
            if (nPage > (block * pager.blocksPerPage)) {
                nextBlock(pager);
            } else {
                page(nPage, pager);
            }

        };

        const nextBlock = (pager) => {

            const block = pager.block + 1;
            pager.block = block;

            const nPage = ((block - 1) * pager.blocksPerPage) + 1;
            pager.page = nPage;

            pager.initialPage = nPage;

            const endPage = (pager.initialPage + pager.blocksPerPage) - 1;

            if (endPage <= pager.totalPages) {
                pager.endPage = endPage;
            } else {
                pager.endPage = pager.totalPages;
            }

            page(nPage, pager);

        }

        const page = (page, pager) => {
            pager.page = page;
            conf.value = pager;
            // createPager(pager, props.totaldata);
        };

        return {
            conf,
            classPager,
            buttonPreviousPage,
            previousPage,
            range,
            page,
            buttonNextPage,
            nextPage,
        }

    }
}

</script>