<template>
    <section id="contact">

        <div class="contact animate__bounceIn">

            <div class="container contact-container d-flex">

                <div class="col-4 p-4">

                    <div class="info card">

                        <div class="info-item d-flex">
                            <i class="fa-solid fa-location-dot"></i>
                            <div>
                                <h4> Dirección </h4>
                                <p> Cuernavaca, Morelos, México Cp. 62370 </p>
                            </div>
                        </div>

                        <div class="info-item d-flex">
                            <i class="fa-solid fa-envelope"></i>
                            <div>
                                <h4> Correo </h4>
                                <p> fernandojacobo54@gmail.com </p>
                            </div>
                        </div>

                        <div class="info-item d-flex">
                            <i class="fa-solid fa-mobile-screen-button"></i>
                            <div>
                                <h4> Teléfono </h4>
                                <p> 777 494 25 15 </p>
                            </div>
                        </div>

                    </div>

                </div>

                <div class="col-8 p-4">

                    <form action="#" method="POST" role="form" class="contact-form" autocomplete="off">
                    
                        <div class="row">
                            <div class="col-md-6 form-group">
                                <input type="text" name="nombre" class="form-control" id="nombre" placeholder="Tu Nombre" v-model="contactForm.nombre" @change="validInput('nombre')">
                                <small class="text-danger fw-semibold" id="msg-nombre"></small>
                            </div>
                            <div class="col-md-6 form-group mt-3 mt-md-0">
                                <input type="email" class="form-control" name="correo" id="correo" placeholder="Tu Correo" v-model="contactForm.correo" @change="validInput('correo')">
                                <small class="text-danger fw-semibold" id="msg-correo"></small>
                            </div>
                        </div>

                        <div class="form-group mt-3">
                            <input type="text" class="form-control" name="asunto" id="asunto" placeholder="Asunto" v-model="contactForm.asunto" @change="validInput('asunto')">
                            <small class="text-danger fw-semibold" id="msg-asunto"></small>
                        </div>
                        
                        <div class="form-group mt-3">
                            <textarea class="form-control" name="mensaje" id="mensaje" placeholder="Mensaje" v-model="contactForm.mensaje" @change="validInput('mensaje')"></textarea>
                            <small class="text-danger fw-semibold" id="msg-mensaje"></small>
                        </div>
                        
                        <div class="text-center">
                            <button type="button" @click="validForm()"> Enviar Mensaje </button>
                        </div>
                    
                    </form>
                    
                </div>

            </div>

        </div>

    </section>
</template>

<script>

import { onMounted, ref } from 'vue';
import { useStore } from 'vuex';

export default {
    name: 'ContactView',
    components: {},
    setup () {

        const store = useStore();

        const contactForm = ref({
            nombre: '',
            correo: '',
            asunto: '',
            mensaje: '',
        })

        onMounted( () => {
            
            window.scrollTo(0, 0);

            const nav = document.getElementById('nav');
            nav.classList.remove('show');

        });

        const validInput = (id) => {
            const form = contactForm.value;
            if (form[id] !== '') {
                document.getElementById('msg-' + id).innerHTML = '';
            }
        };

        const validForm = () => {
            
            const form = contactForm.value;
            
            for (const key in form) {
                if (Object.hasOwnProperty.call(form, key)) {
                    const value = form[key];
                    if (value.length === 0) {
                        document.getElementById('msg-' + key).innerHTML = 'Campo requerido';
                        document.getElementById(key).focus();
                        return false;
                    } else {
                        document.getElementById('msg-' + key).innerHTML = '';
                    }
                }
            }

            setTimeout(() => {
                store.commit('showAlert', {
                    title: 'ÉXITO',
                    icon: 'success',
                    message: 'Mensaje enviado con éxito.',
                });
            }, 1000);

            contactForm.value = {
                nombre: '',
                correo: '',
                asunto: '',
                mensaje: '',
            };

        }

        return {
            contactForm,
            validForm,
            validInput,
        }

    }
}

</script>