<template>
    <section id="blog">

        <div class="blog">

            <div class="container-fluid">

                <template v-if="showBlog === false">
                    <div class="row w-100">
                        <LoaderApp />
                    </div>
                </template>

                <template v-else>
                    <div class="div-blog">

                        <div class="row w-100">

                            <div class="col-sm-12 col-md-4 col-xl-3 pe-5 ps-5 mb-3 div-filters-posts">
                                <div class="card">
                                    
                                    <div class="accordion accordion-flush">

                                        <div class="accordion-item">
                                            <h2 class="accordion-header" id="categories">
                                                <button class="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#flush-categories" aria-expanded="false" aria-controls="flush-categories">
                                                    <i class="fa-solid fa-chevron-right me-2"></i> Categorías
                                                </button>
                                            </h2>
                                            <div id="flush-categories" class="accordion-collapse collapse show" aria-labelledby="categories" data-bs-parent="#categories">
                                                <ul class="list-group">
                                                    
                                                    <li id="li-All" class="list-group-item li-categories active">
                                                        <a href="#" class="p-2" @click="GetPostsByCategorie('All')"> Todas </a>
                                                    </li>
                                                    
                                                    <template v-if="categories.length > 0">
                                                        <li v-for="categorie in  categories" :key="categorie.id" :id="'li-' + categorie.id" class="list-group-item li-categories d-flex flex-row justify-content-between">
                                                            <a href="#" class="p-2" @click="GetPostsByCategorie(categorie.name)">
                                                                {{ categorie.name }}
                                                            </a>
                                                            <span v-if="(parseFloat(categorie.totalPosts) > 0) && (parseFloat(categorie.totalPosts) <= 99)" class="badge bg-secondary"> {{ categorie.totalPosts }} </span>
                                                            <span v-if="(parseFloat(categorie.totalPosts) > 99)" class="badge bg-secondary"> 99+ </span>
                                                        </li>
                                                    </template>

                                                </ul>
                                            </div>
                                        </div>

                                    </div>

                                </div>
                            </div>

                            <div class="col-sm-12 col-md-8 col-xl-9 pe-5 ps-5 mb-3 div-posts">

                                <div class="d-flex justify-content-between">

                                    <div class="header-blog">
                                        <nav style="--bs-breadcrumb-divider: '>';" aria-label="breadcrumb">
                                            <ol class="breadcrumb">
                                                <li class="breadcrumb-item">
                                                    <router-link to="/blog"> Blog </router-link>
                                                </li>
                                            </ol>
                                        </nav>
                                    </div>

                                    <div class="btns-views">
                                        <button type="button" :class="BtnViewClasses('Grid') + ' me-2'" @click="ChageViewPosts('Grid')">
                                            <i class="fa-solid fa-grip-vertical"></i>
                                        </button>
                                        <button type="button" :class="BtnViewClasses('List')" @click="ChageViewPosts('List')">
                                            <i class="fa-solid fa-grip-lines"></i>
                                        </button>
                                    </div>

                                </div>

                                <template v-if="showPosts === false">
                                    <div class="row w-100">
                                        <LoaderApp />
                                    </div>
                                </template>

                                <template v-else>

                                    <template v-if="viewPosts === 'Grid'">

                                        <div class="container-view-grid">

                                            <template v-for="post in posts" :key="post.id" >
                                                <div v-show="post.page === pager.page" class="card-post col-sm-12 col-md-4 p-2">
                                                    
                                                    <div class="post-container-grid">
                                                        
                                                        <div class="post-img">
                                                            <span :id="'span-' + post.id" class="placeholder" style="height: 250px; display: block;"></span>
                                                            <img :id="'img-' + post.id" :src="'images/blog/' + post['image']" class="img-fluid rounded" :alt="post.title" @load="LoadImg(post.id)" style="display: none;" />
                                                        </div>
                                                        
                                                        <div class="author d-flex justify-content-between">
                                                            <small class="text-muted">
                                                                <i class="fa-solid fa-calendar-days me-2"></i>
                                                                {{ post.createdAt }}
                                                            </small>
                                                            <small v-if="post.categories.length > 0" class="text-muted">
                                                                <i class="fa-solid fa-tag me-2"></i>
                                                                {{ post.categories.join(', ') }}
                                                            </small>
                                                        </div>

                                                        <h3 class="post-title"> {{ post.title }} </h3>
                                                        
                                                        <p> {{ post.description }} </p>

                                                        <div class="d-flex justify-content-between">
                                                            
                                                            <div class="reactions"></div>

                                                            <div class="read-more">
                                                                <button type="button" class="btn btn-outline-secondary btn-sm btn-readmore" @click="PostDetail(post.id)">
                                                                    Leer más
                                                                    <i class="fa-solid fa-arrow-right-long"></i>
                                                                </button>
                                                            </div>

                                                        </div>

                                                    </div>

                                                </div>
                                            </template>

                                        </div>

                                    </template>

                                    <template v-if="viewPosts === 'List'">
                                        
                                        <div class="container-view-list">
                                            
                                            <template v-for="post in posts" :key="post.id" >
                                                <div v-show="post.page === pager.page" class="row mt-3 mb-3">
                                                    
                                                    <div class="post-container-list">
                                                        
                                                        <div class="post-img-list">
                                                            <span :id="'span-' + post.id" class="placeholder" style="height: 180px; display: block;"></span>
                                                            <img :id="'img-' + post.id" :src="'images/blog/' + post['image']" class="img-fluid rounded" :alt="post.title" @load="LoadImg(post.id)" style="display: none;" />
                                                        </div>

                                                        <div class="post-details w-100 ps-3">
                                                        
                                                            <div class="author d-flex justify-content-between">
                                                                <small class="text-muted">
                                                                    <i class="fa-solid fa-calendar-days me-2"></i> {{ post.createdAt }}
                                                                </small>
                                                                <small class="text-muted">
                                                                    <i class="fa-solid fa-tag me-2"></i> {{ post.categories.join(', ') }}
                                                                </small>
                                                            </div>

                                                            <h3 class="post-title"> {{ post.title }} </h3>
                                                            
                                                            <p> {{ post.description }} </p>

                                                            <div class="d-flex justify-content-between">
                                                            
                                                                <div class="reactions"></div>

                                                                <div>
                                                                    <button class="btn btn-outline-secondary btn-sm btn-readmore" @click="PostDetail(post.id)">
                                                                        Leer más
                                                                        <i class="fa-solid fa-arrow-right-long"></i>
                                                                    </button>
                                                                </div>

                                                            </div>

                                                        </div>

                                                    </div>

                                                </div>
                                            </template>

                                        </div>

                                    </template>

                                    <div class="container mt-4">
                                        <PagerApp ref="pager-posts" :pagerconf="pager" :totaldata="pager.total" />
                                    </div>

                                </template>

                            </div>

                        </div>

                    </div>
                </template>

            </div>

        </div>

    </section>
</template>

<script>

import router from '@/router';
import { onMounted, ref, watch } from 'vue';

import { useStore } from 'vuex';

import LoaderApp from '../components/Loader.vue';
import PagerApp from '../components/Pager.vue';

export default {
    name: 'BlogView',
    components: {
        LoaderApp,
        PagerApp,
    },
    setup () {
        
        const store = useStore();
        const state = store.state;

        const blog = state.blog;

        let categories = ref([]);
        let posts = ref([]);

        let showBlog = ref(false);
        let showCategories = ref(false);
        let showPosts = ref(false);

        let viewPosts = ref('Grid');

        const pagerConf = {
            block: 1,
            blocksPerPage: 3,
            endPage: 1,
            page: 1,
            totalBlocks: 1,
            total: 0,
            totalPages: 1,
            totalPerPages: 3,
            initialPage: 1,
            limit: 3
        };

        const pager = ref(pagerConf);

        onMounted(() => {
            
            const nav = document.getElementById('nav');
            nav.classList.remove('show');

            window.scrollTo(0, 0);

            GetPostsCategories();
            
            GetPosts();

            setTimeout(() => {
                if ((categories.value.length > 0) || (posts.value.length > 0)) {
                    showBlog.value = true;

                    if (posts.value.length > 0) {
                        showPosts.value = true;
                    }
                } else {
                    showBlog.value = false;
                }
            }, 1000);

        });

        watch(pager.value, () => {
            CreatePager();
        });

        const BtnViewClasses = (viewB) => {
            const viewP = viewPosts.value;
            if (viewB === viewP) {
                return 'btn btn-outline-secondary btn-sm btn-view active'
            } else {
                return 'btn btn-outline-secondary btn-sm btn-view'
            }
        }

        const ChageViewPosts = (view) => {
            if (viewPosts.value !== view) {
                viewPosts.value = view;
            }
        }

        const CreatePager = () => {
            const cPager = pager.value;

            //
            const totalPages = Math.ceil(parseFloat(cPager.total) / cPager.limit);
            cPager.totalPages = totalPages;

            //
            const totalBlocks = Math.ceil(totalPages / cPager.blocksPerPage);
            cPager.totalBlocks = totalBlocks;

            //
            const block = Math.ceil(cPager.page / cPager.blocksPerPage);
            cPager.block = block;

            //
            if (block === 1) {
                if (totalPages <= cPager.blocksPerPage) {
                    cPager.endPage = totalPages;
                } else {
                    cPager.endPage = cPager.blocksPerPage;
                }
            }
        };

        const GetPostsCategories = () => {
            /*  */
        }

        const GetPosts = () => {
            blog.posts = [];
            posts.value = [];
            
            pager.value = pagerConf;
            
            const cPager = pager.value;
            
            const data = {
                filters: {},
                page: cPager.page,
                totalPerPages: cPager.totalPerPages,
            };

            store.commit('GetPosts', data);
            
            posts.value = blog.posts;
            cPager.total = posts.value.length;
        }

        const GetPostsByCategorie = (cat) => {

            posts.value = [];
            showPosts.value = false;

            if (cat === 'All') {
                GetPosts();
            }

            setTimeout(() => {
                if (posts.value.length > 0) {
                    showPosts.value = true;
                } else {
                    showPosts.value = false;
                }
            }, 1000);

        }

        const LoadImg = (id) => {
            document.getElementById('span-' + id).style.display = 'none';
            document.getElementById('img-' + id).style.display = 'block';
        }

        const PostDetail = (id) => {
            router.push('/post-detail/' + id);
        }

        return {
            BtnViewClasses,
            categories,
            ChageViewPosts,
            GetPosts,
            GetPostsByCategorie,
            LoadImg,
            pager,
            posts,
            PostDetail,
            showBlog,
            showCategories,
            showPosts,
            viewPosts
        }

    }
}

</script>