<template>
    <section id="portfolio">

        <div class="portfolio animate__bounceIn">

            <div class="row">
                <div class="pe-5 ps-5">
                    <nav style="--bs-breadcrumb-divider: '>';" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item">
                                <router-link to="/portfolio"> Portafolio </router-link>
                            </li>
                            <li class="breadcrumb-item active" aria-current="page"> Proyectos Desarrollados / Participado. </li>
                        </ol>
                    </nav>
                </div>
            </div>

            <div v-if="showProjects === false" class="container-fluid">
                
                <div class="container">

                    <LoaderApp v-if="message === ''" />

                    <div v-if="message !== ''" class="alert alert-secondary text-center" role="alert"> {{ message }} </div>

                </div>

            </div>

            <div v-if="showProjects === true" class="container-fluid">

                <div class="container-fluid">

                    <div class="d-flex flex-row flex-wrap">
                        
                        <template v-for="project in projects" :key="project.id">
                            <div v-if="project.page === pager.page" class="col-12 col-md-6 col-xl-4 mb-3">
                                
                                <div class="portfolio-item">
                                    
                                    <div class="project-img">
                                        <span :id="'span-' + project.id" class="placeholder" style="height: 250px; display: block;"></span>
                                        <img :id="'img-' + project.id" :src="'images/portfolio/' + project['image']" class="img-fluid" :alt="project.title" @load="LoadImg(project.id)" style="display: none;" />
                                    </div>

                                    <div class="details position-relative">
                                        
                                        <div class="icon">
                                            <i v-if="project.type === 'Web'" class="fa-solid fa-desktop"></i>
                                            <i v-if="project.type === 'App'" class="fa-solid fa-mobile-screen-button"></i>
                                        </div>

                                        <h3> {{ project.title }} </h3>

                                        <p v-if="project.description.length > 100" class="mt-3 mb-3">
                                            {{ project.description.substr(0, 97) }}...
                                        </p>

                                        <p v-else class="mt-3 mb-3">
                                            {{ project.description }}
                                        </p>

                                        <div v-if="project.technologies.length > 0" class="d-flex flex-column mb-4">
                                            <div class="technologies">
                                                <span v-for="technologie in project.technologies" :key="technologie.id" class="badge text-bg-secondary me-2">
                                                    <i :class="IconClass(technologie.id)"></i>
                                                    {{ technologie.name }}
                                                </span>
                                            </div>                                            
                                        </div>

                                        <div class="actions">
                                            <div class="d-flex justify-content-between">
                                                <small class="text-muted">
                                                    <a href="#" @click="ProjectDetail(project)">
                                                        <i class="fa-solid fa-folder"></i>
                                                        Detalles
                                                    </a>
                                                </small>
                                                <small v-if="project.url !== ''" class="text-muted">
                                                    <a :href="project.url" target="_blank">
                                                        Link del sitio
                                                        <i class="fa-solid fa-link"></i>
                                                    </a>
                                                </small>
                                            </div>
                                        </div>

                                    </div>
                                    
                                </div>

                            </div>
                        </template>          

                    </div>

                </div>

                <div class="container">
                    <PagerApp ref="pager-projects" :pagerconf="pager" :totaldata="pager.total" />
                </div>

            </div>

        </div>

        <div id="modals">
            
            <div class="modal fade" id="project-details" tabindex="-1" aria-labelledby="project-details-title" aria-hidden="true">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        
                        <div class="modal-header">
                            <h5 class="modal-title" id="project-details-title"> {{ projectModal.title }} </h5>
                            <a class="btn btn-close" data-bs-dismiss="modal" aria-label="Close">
                                <i class="fa-solid fa-circle-xmark"></i>
                            </a>
                        </div>
                        
                        <div class="modal-body">
                            <div class="card">
                                    
                                <div class="card-carousel">
                                    <div id="carousel-modal" class="carousel slide" data-bs-ride="true">
                                        
                                        <div v-if="projectModal.images.length === 1" class="carousel-indicators">
                                            <button type="button" data-bs-target="#carousel-modal" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                                        </div>

                                        <div v-if="projectModal.images.length > 1" class="carousel-indicators">
                                            <button v-for="(image, index) in projectModal.images" :key="index" type="button" data-bs-target="#carousel-modal" :data-bs-slide-to="index" :class="index === 0 ? 'active' : ''" aria-current="true" :aria-label="'Slide' + index"></button>
                                        </div>
                                        
                                        <div class="carousel-inner" v-if="projectModal.images.length > 1">
                                            <div v-for="(image, index) in projectModal.images" :key="index" :class="index === 0 ? 'carousel-item active' : 'carousel-item'">
                                                <img :src="'images/portfolio/' + image" class="d-block w-100" :alt="projectModal.title + '-' + index" :title="projectModal.title + '-' + index" />
                                            </div>
                                        </div>

                                        <div class="carousel-inner" v-if="projectModal.images.length <= 0">
                                            <img :src="'images/portfolio/' + projectModal.image" class="d-block w-100" :alt="projectModal.title + '-0'" :title="projectModal.title + '-0'" />
                                        </div>
                                        
                                        <button v-if="projectModal.images.length > 1" class="carousel-control-prev" type="button" data-bs-target="#carousel-modal" data-bs-slide="prev">
                                            <i class="fa-solid fa-chevron-left"></i>
                                        </button>
                                        
                                        <button v-if="projectModal.images.length > 1" class="carousel-control-next" type="button" data-bs-target="#carousel-modal" data-bs-slide="next">
                                            <i class="fa-solid fa-chevron-right"></i>
                                        </button>

                                    </div>
                                </div>

                                <div class="card-body">

                                    <div class="mb-3">
                                        <small class="text-muted"> Descripción: </small>
                                        <p class="card-text">
                                            {{ projectModal.description }}
                                        </p>
                                    </div>

                                    <div v-if="projectModal.technologies.length > 0" class="d-flex flex-column mt-4 mb-4">
                                        <small class="text-muted me-auto"> Tecnologías Aplicadas: </small>
                                        <div class="technologies">
                                            <span v-for="technologie in projectModal.technologies" :key="technologie.id" class="badge text-bg-secondary me-2">
                                                <i :class="IconClass(technologie.id)"></i>
                                                {{ technologie.name }}
                                            </span>
                                        </div>                                            
                                    </div>

                                    <div class="mb-3 d-flex justify-content-between">
                                        <small v-if="projectModal.url !== ''" class="text-muted ms-auto"> 
                                            <a :href="projectModal.url" target="_blank">
                                                Link del sitio
                                                <i class="fa-solid fa-link"></i>
                                            </a>
                                        </small>
                                    </div>

                                </div>

                            </div>
                        </div>
                        
                        <!-- <div class="modal-footer"></div> -->

                    </div>
                </div>
            </div>

        </div>
        
    </section>
</template>

<script>


import * as bootstrap from 'bootstrap';

import { onMounted, ref, watch } from 'vue';

import { useStore } from 'vuex';

import LoaderApp from '../components/Loader.vue';

import PagerApp from '../components/Pager.vue';

export default {
    name: 'PortfolioView',
    components: {
        LoaderApp,
        PagerApp
    },
    setup () {
        
        const store = useStore();
        const state = store.state;

        const portfolio = state.portfolio;

        const projects = ref([]);
        const projectModal = ref({
            description: '',
            image: '',
            images: [],
            technologies: [],
            title: '',
            url: '',
        });
        
        const message = ref('');

        const showProjects = ref(true);

        const pagerConf = {
            block: 1,
            blocksPerPage: 3,
            endPage: 1,
            page: 1,
            totalBlocks: 1,
            total: 0,
            totalPages: 1,
            totalPerPages: 3,
            initialPage: 1,
            limit: 3
        };

        const pager = ref(pagerConf);

        onMounted(() => {

            const nav = document.getElementById('nav');
            nav.classList.remove('show');

            window.scrollTo(0, 0);

            GetProjects();
            
        });

        watch(pager.value, () => {
            CreatePager();
        });

        const CreatePager = () => {
            const cPager = pager.value;

            //
            const totalPages = Math.ceil(parseFloat(cPager.total) / cPager.limit);
            cPager.totalPages = totalPages;

            //
            const totalBlocks = Math.ceil(totalPages / cPager.blocksPerPage);
            cPager.totalBlocks = totalBlocks;

            //
            const block = Math.ceil(cPager.page / cPager.blocksPerPage);
            cPager.block = block;

            //
            if (block === 1) {
                if (totalPages <= cPager.blocksPerPage) {
                    cPager.endPage = totalPages;
                } else {
                    cPager.endPage = cPager.blocksPerPage;
                }
            }
        };

        const GetProjects = () => {
            portfolio.projects = [];
            projects.value = [];
            pager.value = pagerConf;
            const cPager = pager.value;
            const data = {
                filters: {},
                page: cPager.page,
                totalPerPages: cPager.totalPerPages,
            };
            store.commit('GetProjects', data);
            projects.value = portfolio.projects;
            cPager.total = projects.value.length;
        }

        const IconClass = (ic) => {
            const icons = state.icons;
            if (icons.length > 0) {
                for (const key in icons) {
                    if (Object.hasOwnProperty.call(icons, key)) {
                        const icon = icons[key];
                        if (icon.id === ic) {
                            return icon.class;
                        }
                    }
                }
            }
        };

        const LoadImg = (id) => {
            document.getElementById('span-' + id).style.display = 'none';
            document.getElementById('img-' + id).style.display = 'block';
        }

        const ShowModal = (id) => {
            const modal = new bootstrap.Modal('#' + id);
            modal.show();
        };

        const ProjectDetail = (proj) => {

            projectModal.value.description = proj.description;
            projectModal.value.image = proj.image;
            projectModal.value.images = proj.images;
            projectModal.value.technologies = proj.technologies;
            projectModal.value.title = proj.title;
            projectModal.value.url = proj.url;

            ShowModal('project-details');

        }

        return {
            IconClass,
            LoadImg,
            message,
            pager,
            projectModal,
            projects,
            ProjectDetail,
            showProjects,
            ShowModal,
        }

    }
}

</script>