import { createStore } from 'vuex';

import * as portfolio from '@/assets/js/portfolio.js';
import * as blog from '@/assets/js/blog.js';
// import * as aboutMe from '@/assets/js/aboutMe.js';

export default createStore({
    state: {
        aboutMe: {
            skills: [
                {
                    id: 'HTML',
                    color: '#E67E22',
                    name: 'HTML 5',
                    percentage: 90,
                },
                {
                    id: 'CSS',
                    color: '#2E86C1',
                    name: 'CSS 3',
                    percentage: 80,
                },
                {
                    id: 'JS',
                    color: '#F1C40F',
                    name: 'Javas Script',
                    percentage: 60,
                },
                {
                    id: 'VUE',
                    color: '#117864',
                    name: 'Vue JS',
                    percentage: 55,
                },
                {
                    id: 'PHP',
                    color: '#AF7AC5',
                    name: 'PHP',
                    percentage: 80,
                },
            ],
        },
        icons: [
            {
                id: 'BOOTSTRAP',
                class: 'fa-brands fa-bootstrap'
            },
            {
                id: 'CSS',
                class: 'fa-brands fa-css3-alt'
            },
            {
                id: 'HTML',
                class: 'fa-brands fa-html5'
            },
            {
                id: 'JS',
                class: 'fa-brands fa-square-js'
            },
            {
                id: 'LARAVEL',
                class: 'fa-brands fa-laravel'
            },
            {
                id: 'MYSQL',
                class: 'fa-solid fa-database'
            },
            {
                id: 'PHP',
                class: 'fa-brands fa-php'
            },
            {
                id: 'VUE',
                class: 'fa-brands fa-vuejs'
            },
        ],
        api: '',
        blog: {
            categories: [],
            totalComments: 0,
            comments: [],
            totalPosts: 0,
            recentPosts: [],
            post: {},
            posts: [],
        },
        portfolio: {
            projects: [],
        },
    },
    getters: {
    },
    mutations: {
        GetProjects: (state, data) => {

            // Sustituir por petición
            const projects = portfolio.projects;

            let i = 1;
            let page = data.page;
            for (const key in projects) {
                if (Object.hasOwnProperty.call(projects, key)) {
                    const project = projects[key];
                    project.page = page;
                    state.portfolio.projects.push(project);
                    if (i < data.totalPerPages) {
                        i ++;
                    } else {
                        i = 1;
                        page ++;
                    }
                }
            }

        },
        GetPost: (state, id) => {

            state.blog.post = {};

            const posts = state.blog.posts;

            if (posts.length > 0) {
                const arrPosts = posts.filter(key => key.id === id );
                if (arrPosts.length > 0) {
                    const post = arrPosts[0];
                    state.blog.post = post;
                }
            }

        },
        GetPosts: async (state, data) => {

            // Sustituir por petición
            const posts = blog.posts;

            let i = 1;
            let page = data.page;
            for (const key in posts) {
                if (Object.hasOwnProperty.call(posts, key)) {
                    const post = posts[key];
                    post.page = page;
                    state.blog.posts.push(post);
                    if (i < data.totalPerPages) {
                        i ++;
                    } else {
                        i = 1;
                        page ++;
                    }
                }
            }

        },
    },
    actions: {
        /*  */
    },
    modules: {
        /*  */
    }
})
