<script>

import { onMounted } from 'vue';
import { useStore } from 'vuex';

export default {
    name: 'AboutMeView',
    setup () {

        const store = useStore();
        const state = store.state;

        const icons = state.icons;

        const skills = state.aboutMe.skills;

        onMounted( () => {
            
            window.scrollTo(0, 0);

            const nav = document.getElementById('nav');
            nav.classList.remove('show');

        });

        const GetIcon = (id) => {
            const cIcon = icons.filter(key => key.id === id);
            if (cIcon.length > 0) {
                const icon = cIcon[0];
                return icon.class;
            }
            return '';
        };

        return {
            GetIcon,
            skills,
        }

    }
}

</script>