<template>
    <section id="post-detail">

        <div class="container-post-detail">

            <template v-if="Object.keys(post).length === 0">
                <LoaderApp />
            </template>

            <template v-if="Object.keys(post).length > 0">
            
                <div class="container animate__bounceIn">
                    <div class="mb-3">
                        <nav style="--bs-breadcrumb-divider: '>';" aria-label="breadcrumb">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <router-link to="/blog"> Blog </router-link>
                                </li>
                                <li class="breadcrumb-item active" aria-current="page"> <b> {{ post.title }} </b> </li>
                            </ol>
                        </nav>
                    </div>
                </div>

                <div class="container animate__bounceIn">

                    <div class="d-flex flex-row">

                        <div class="col-12 col-sm-12 col-md-8">

                            <div class="d-flex justify-content-between p-2">
                                <small class="text-muted">
                                    <i class="fa-solid fa-calendar-days me-2"></i> {{ post.createdAt }}
                                </small>
                                <small class="text-muted">
                                    <i class="fa-solid fa-user-pen me-2"></i> {{ post.author }}
                                </small>
                            </div>

                            <div class="card mb-3">

                                <div class="p-3">
                                    <img :src="'../images/blog/' + post['image']" class="img-fluid rounded" :alt="post.title" :title="post.title" />
                                </div>

                                <div class="card-body">
                            
                                    <h3 class="card-title">
                                        {{ post.title }}
                                    </h3>

                                    <div class="card-text" v-html="post.body"></div>
                                    
                                    <div class="card-text">
                                        <div class="d-flex justify-content-between">
                                            <small class="text-muted"></small>
                                            <small v-if="post.categories.length > 0" class="text-muted">
                                                <i class="fa-solid fa-tag me-2"></i>
                                                {{ post.categories.join(', ') }}
                                            </small>
                                        </div>
                                    </div>

                                </div>

                            </div>

                        </div>

                        <div class="col-12 col-sm-12 col-md-4">

                            <div v-if="otherPosts.length > 0" class="publicaciones-recientes">
                                
                                <div class="d-flex justify-content-between p-2">
                                    <small class="text-muted">
                                        Otras publicaciones
                                    </small>
                                </div>

                                <div class="p-2">
                                    <div id="otherPosts" class="carousel slide" data-bs-ride="carousel">
                                        <div class="carousel-inner">

                                            <div v-for="(rPost, index) in otherPosts" :key="rPost.id" :class="index === 0 ? 'carousel-item active' : 'carousel-item' ">
                                                <div class="card" style="max-width: 540px;">
                                                    <div class="row g-0">
                                                        
                                                        <div class="col-12">
                                                            <div class="card-body">
                                                                
                                                                <h5 class="card-title"> {{ post.title }} </h5>

                                                                <p class="card-text text-truncate" v-html="post.description"></p>

                                                                <p class="card-text d-flex justify-content-end">
                                                                    <small class="text-muted">
                                                                        <a href="#">
                                                                            Leer más
                                                                            <i class="fa-solid fa-arrow-right-long"></i>
                                                                        </a>
                                                                    </small>
                                                                </p>

                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>
                                            </div>
                                    
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div v-if="publicity.length > 0" class="publicidad-pagada">

                                <div class="d-flex justify-content-between p-2">
                                    <small class="text-muted">
                                        Públicidad pagadada
                                    </small>
                                </div>

                                <div class="p-2">
                                    <div class="card mb-3">
                                        
                                        <div class="img-top-card p-2">
                                            <img :src="'../images/blog/' + post['image']" class="img-fluid rounded" :alt="post.title" :title="post.title" />
                                        </div>

                                        <div class="card-body">
                                            <h5 class="card-title"> CEMEFI - Centro de Médicina  </h5>
                                        </div>

                                    </div>
                                </div>

                            </div>

                        </div>

                    </div>

                </div>
            
            </template>

        </div>

    </section>
</template>

<script>

import router from '@/router';
import { onMounted, ref } from 'vue';

import { useStore } from 'vuex';

import LoaderApp from '../components/Loader.vue';
// import PagerApp from '../components/Pager.vue';

export default {
    props: [ 'id' ],
    name: 'PostDetailView',
    components: {
        // PagerApp,
        LoaderApp,
    },
    setup (props) {

        const store = useStore();
        const state = store.state;

        const blog = state.blog;

        let post = ref({});

        let comments = ref([]);
        
        let messagePost = ref('');

        let otherPosts = ref([]);

        const pagerConf = {
            block: 1,
            blocksPerPage: 2,
            endPage: 1,
            page: 1,
            totalBlocks: 1,
            total: 0,
            totalPages: 1,
            initialPage: 1,
            limit: 2
        };
        
        let pagerComments = ref(pagerConf);

        let publicity = ref([]);

        onMounted( () => {
            
            window.scrollTo(0, 0);

            const nav = document.getElementById('nav');
            nav.classList.remove('show');

            if ((!props.id) && (props.id === '')) {
                router.push('/blog');
            }

            store.commit('GetPost', props.id);

            setTimeout(() => {
                post.value = {};
                if (Object.keys(blog.post).length > 0) {
                    post.value = blog.post;
                }
            }, 1000);
            
        });

        return {
            comments,
            messagePost,
            post,
            pagerComments,
            publicity,
            otherPosts,
        }

    }
}

</script>