<template>

    <header>

        <NavBar />

    </header>

    <main>
        
        <router-view />

    </main>

    <Footer />

</template>

<script>

import 'bootstrap';

import NavBar from './components/NavBar.vue';
import Footer from './components/Footer.vue';

import { onMounted } from 'vue';

export default {
    name: 'HomeView',
    components: {
        NavBar,
        Footer
    },
    setup () {

        onMounted(() => {

            window.addEventListener('scroll', () => {
                const scrollY = window.scrollY;
                
                const navBg = document.getElementById('nav-bg');
                const imgLogo = document.getElementById('img-logo');
                
                if (scrollY > 0) {
                    navBg.classList.add('sticked');
                    imgLogo.src = '/images/logo-white.png';
                } else {
                    navBg.classList.remove('sticked');
                    imgLogo.src = '/images/logo-blue.png';
                }

            });
            
        });
        
    }
}

</script>

<style>

@import '~bootstrap/dist/css/bootstrap.css';

</style>
